<template>
	<div>
		<!-- 图片 -->
		<div style="text-align: center;">
			<img class="logo" src="../../static/img/logo.png"/>
		</div>
		
		<!-- 周日奖池 -->
		<div class="jackpot-layout">
			<div class="jackpot-pot pot1">
				<div>{{$t('Day-prize-pool')}}({{dayPoolId}})</div>
				<div>
					<span class="all-time">{{dayDownTime.h}}</span>
					<span> : </span>
					<span class="all-time">{{dayDownTime.m}}</span>
					<span> : </span>
					<span class="all-time">{{dayDownTime.s}}</span>
				</div>
				<div class="all-time">{{dayUsdtAmount}} USDT</div>
			</div>
			<div class="jackpot-pot">
				<div>{{$t('Week-prize-pool')}}({{weekPoolId}})</div>
				<div>
					<span class="all-time">{{weekDownTime.d}}</span>
					<span> : </span>
					<span class="all-time">{{weekDownTime.h}}</span>
					<span> : </span>
					<span class="all-time">{{weekDownTime.m}}</span>
					<span> : </span>
					<span class="all-time">{{weekDownTime.s}}</span>
				</div>
				<div class="all-time">{{weekUsdtAmount}} USDT</div>
			</div>
		</div>
		
		<!-- 输入框和选择U -->
		<div class="filling-layout">
			<div class="filling-tips">
				Token: ECO{{$t('Resonance-ratio')}} 300U:{{ecoRatio}}ECO
			</div>
			<div class="filling-input-layout">
				<input class="filling-input" v-model="usdtInput" @change="inputChange" type="number"/>
				<div class="filling-usdt">USDT</div>
				<div class="filling-approve all-pointer" @click="sendOut()">{{$t('Send-out')}}</div>
			</div>
			<div v-if="!isApprove1 || !isApprove2" style="display: flex; justify-content: center;">
				<div v-if="!isApprove1" class="filling-approve2 all-pointer" @click="approveUsdt()">{{$t('Approval')}} USDT</div>
				<div v-if="!isApprove2" class="filling-approve2 all-pointer" @click="approveEco()">{{$t('Approval')}} ECO</div>
			</div>
			<div class="filling-u">
				<div @click="selectU(0)" :class="selectColor==='0'?'colorU2':'colorU'">100U</div>
				<div @click="selectU(1)" :class="selectColor==='1'?'colorU2':'colorU'">300U</div>
				<div @click="selectU(2)" :class="selectColor==='2'?'colorU2':'colorU'">500U</div>
				<div @click="selectU(3)" :class="selectColor==='3'?'colorU2':'colorU'">1000U</div>
				<div @click="selectU(4)" :class="selectColor==='4'?'colorU2':'colorU'">1500U</div>
				<div @click="selectU(5)" :class="selectColor==='5'?'colorU2':'colorU'">2000U</div>
				<div @click="selectU(6)" :class="selectColor==='6'?'colorU2':'colorU'">2500U</div>
				<div @click="selectU(7)" :class="selectColor==='7'?'colorU2':'colorU'">5000U</div>
			</div>
		</div>
		
		<!-- 余下全部 -->
		<!-- 第一块 -->
		<div class="all-w">
			<div class="all-right-two">
				<div class="v4">
					<div :class="v.nodeLevel===1?'v2':'v'">v1</div>
					<div :class="v.nodeLevel===2?'v2':'v'">v2</div>
					<div :class="v.nodeLevel===3?'v2':'v'">v3</div>
					<div :class="v.nodeLevel===4?'v2':'v'">v4</div>
				</div>
				<div>{{$t('Count-Down')}} <span class="all-time">{{v.cd100DayString}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div  class="all-right-two">
				<div>{{v.alreadyIncome}}</div>
				<div>{{$t('Withdrawn-Income')}}</div>
			</div>
			<div class="all-hr"></div>
			<div  class="all-right-two">
				<div>{{v.dayPoolIncome}}</div>
				<div>{{$t('Daily-bonus-pool-income')}} <span @click="poolWithdraw()" class="all-btn-2">{{$t('Withdrawal')}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v.weekPoolIncome}}</div>
				<div>{{$t('Weekly-bonus-pool-income')}} <span @click="poolWithdraw()" class="all-btn-2">{{$t('Withdrawal')}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v.dynamicIncome}}</div>
				<div>{{$t('Dynamic-income')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v.reward}}</div>
				<div>{{$t('Dividend-income')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v.cdTimeString}}</div>
				<div>{{$t('Dividend-countdown')}} <span class="all-time">{{v.timeReward}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v.deposit}}</div>
				<div>{{$t('Deposit-Funds')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v.AcceleratedExit}}</div>
				<div>{{$t('Accelerate-out')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v.untakeIncome}}</div>
				<div>{{$t('Uncollected-income')}}</div>
			</div>
			<div class="all-hr"></div>
			<span @click="smallWithdraw()" class="all-btn all-pointer" style="
			width: 50%; margin: 0 auto; border-radius: 5px; padding: 8px 0;
			">{{$t('Withdrawal')}}</span>
		</div>
		
		<!-- 第二块-邀请链接 -->
		<div class="all-w">
			<div class="all-right-two">
				<div>{{v2.address}}</div>
				<div>{{$t('Invitation-link')}} <span @click="e32()" class="all-btn-2">{{$t('Copy')}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v2.performance}}</div>
				<div>{{$t('Today-invitation-amount')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v2.wholeLine}}</div>
				<div>{{$t('Invitation-performance-of-this-round')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v2.referrer}}</div>
				<div>{{$t('My-inviting-address')}}</div>
			</div>
		</div>
		
		<!-- 第三块-冠军推广业绩 -->
		<div class="all-w">
			<div class="all-right-two">
				<div>{{v3.championPer}}</div>
				<div>{{$t('Today-champion-performance')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v3.championIncome}}</div>
				<div>{{$t('Today-champion-earnings')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div class="v3">
					<div><span class="all-time">{{$t('Champion')}}</span><span> {{v3.dayChampion1}}</span></div>
					<div><span class="all-time">{{$t('Runner-up')}}</span><span> {{v3.dayChampion2}}</span></div>
					<div><span class="all-time">{{$t('Third-gamer')}}</span><span> {{v3.dayChampion3}}</span></div>
				</div>
				<div>{{$t('Winning-address-today')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v3.championWeek0Income}}</div>
				<div>{{$t('Winner-of-last-round')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v3.championWeek1Income}}</div>
				<div>{{$t('Winner-second-of-last-round')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div class="v3">
					<div><span class="all-time">{{$t('Champion')}}</span><span> {{v3.championWeek0}}</span></div>
					<div><span class="all-time">{{$t('Runner-up')}}</span><span> {{v3.championWeek1}}</span></div>
				</div>
				<div>{{$t('Winning-address-of-last-week')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v3.championPer2}}</div>
				<div>{{$t('Winning-performance-of-last-week')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>-</div>
				<div>{{$t('Max-of-this-round')}} <span class="all-btn" style="border-radius: 5px; padding: 3px 5px;">150.00</span></div>
			</div>
		</div>
		
		<!-- 第四块, 保险池子 -->
		<div class="v-layout">
			<div class="v-pot pot1">
				<div style="font-weight: bold;">{{$t('Safe-pool')}}</div>
				<div>
					<span class="all-time">{{v4.d}}</span>
					<span> : </span>
					<span class="all-time">{{v4.h}}</span>
					<span> : </span>
					<span class="all-time">{{v4.m}}</span>
					<span> : </span>
					<span class="all-time">{{v4.s}}</span>
				</div>
				<div class="all-time" style="margin: 0 20px;">{{pool1}} USDT</div>
			</div>
			<div class="v-pot">
				<div style="font-weight: bold;">{{$t('Sustainable-pool')}}</div>
				<div>
					<span class="all-time">{{v4.d}}</span>
					<span> : </span>
					<span class="all-time">{{v4.h}}</span>
					<span> : </span>
					<span class="all-time">{{v4.m}}</span>
					<span> : </span>
					<span class="all-time">{{v4.s}}</span>
				</div>
				<div class="all-time" style="margin: 0 20px;">{{pool2}} USDT</div>
			</div>
		</div>
		
		<!-- 第五块 -->
		<div class="all-w">
			<div class="all-right-two">
				<div class="v4">
					<div :class="v.nodeLevel===1?'v2':'v'">{{$t('Junior')}}</div>
					<div :class="v.nodeLevel===2?'v2':'v'">{{$t('Intermediate')}}</div>
					<div :class="v.nodeLevel===3?'v2':'v'">{{$t('Super')}}</div>
					<div :class="v.nodeLevel===4?'v2':'v'">{{$t('Genesis')}}</div>
				</div>
				<div>{{$t('Global-nodes')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v5.performance}}</div>
				<div>{{$t('Direct-promotion-of-performance')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v5.performance2}}</div>
				<div>{{$t('Total-performance')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div><span class="all-btn" style="border-radius: 5px; padding: 3px 5px;">Small</span> {{v5.small}}</div>
				<div><span class="all-btn" style="border-radius: 5px; padding: 3px 5px;">Max</span> {{v5.max}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v5.gloablNodeIncome}}</div>
				<div>{{$t('Global-node-revenue')}}</div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>3000.000</div>
				<div>{{$t('Node-burn-down')}} <span @click="nodeBurn()" class="all-btn-2">{{$t('Burn-down')}}</span></div>
			</div>
			<div class="all-hr"></div>
			<div class="all-right-two">
				<div>{{v5.nodeIcome}}</div>
				<div>{{$t('Node-burn-down-revenue')}}</div>
			</div>
		</div>
		
	</div>
</template>

<script>
	export default {
		data() {
			return {
				/* 授权按钮的判断 */
				isApprove1: false,
				isApprove2: false,
				
				/* 奖池块 */
				// 日奖池ID
				dayPoolId: 0,
				// 日奖池倒计时
				dayDownTime: {
					h: '00',
					m: '00',
					s: '00'
				},
				// 日奖池USDT数量
				dayUsdtAmount: 0,
				// 周奖池ID
				weekPoolId: 0,
				// 日奖池倒计时
				weekDownTime: {
					d: '00',
					h: '00',
					m: '00',
					s: '00'
				},
				// 日奖池USDT数量
				weekUsdtAmount: 0,
				
				/* 输入框块 */
				// 300U对应比列的ECO
				ecoRatio: '0',
				// 输入框绑定的值
				usdtInput: '',
				// 选中u对应的颜色样式
				selectColor: '',
				
				/* 第一块-v1...v5 */
				// 控制颜色
				v2Color: '',
				v: {
					// 对应主网页 "V1/V2/V3/V4",1=>V1,2=>V2,3=>V3,4=>V4, 普通用户是0, 0不是节点
					nodeLevel: '',
					// 倒计时
					cd100DayString: '00:00:00:00',
					// 已经提现收益
					alreadyIncome: '-',
					// 日奖池收益
					dayPoolIncome: '-',
					// 周奖池收益
					weekPoolIncome: '-',
					// 动态收益
					dynamicIncome: '-',
					// 分红收益
					reward: '-',
					// 下次分红倒计时时分秒
					cdTimeString: '00:00:00',
					// 倒计时分红
					timeReward: '-',
					// 存入资金
					deposit: '-',
					// 加速出局差
					AcceleratedExit: '-',
					// 未提现收益
					untakeIncome: '-',
				},
				v2: {
					// 地址
					address: '-',
					// 复制的完整的地址
					address2: '',
					// 今日邀请金额
					performance: '-',
					// 本轮邀请业绩
					wholeLine: '-',
					// 我的推荐人
					referrer: '-',
				},
				v3: {
					// 今日冠军推广业绩
					championPer: '-',
					// 今日冠军收益
					championIncome: '-',
					// 今日中将地址, 冠军,亚军,季军
					dayChampion1: '-',
					dayChampion2: '-',
					dayChampion3: '-',
					// 上轮周冠军收益
					championWeek0Income: '-',
					// 上轮周季军收益
					championWeek1Income: '-',
					// 上轮奖池地址, 冠军,亚军
					championWeek0: '-',
					championWeek1: '-',
					// 上周冠军邀请业绩
					championPer2: '-'
				},
				
				/* 第四块-保险池子 */
				v4: {
					d: '00',
					h: '00',
					m: '00',
					s: '00',
				},
				pool1: '0',
				pool2: '0',
				
				/* 第五块-全球节点 */
				v5: {
					// 直推业绩
					performance: '-',
					// 合计业绩
					performance2: '-',
					small: '-',
					max: '-',
					// 全球节点收益
					gloablNodeIncome: '-',
					// 节点焚毁收益
					nodeIcome: '-',
				}
				
			}
		},
		
		async created() {
			// 同步锁, 等$isResolve执行完毕了才会执行这个
			await this.$onLaunched;
			this.getAll();
		},
		
		methods: {
			// 获取数据
			async getAll() {
				// 同步锁
				await this.$onLaunched;
				this.isApproveUsdt();
				this.isApproveEco();
				
				this.getDayPoolId();
				this.getWeekPoolId();
				this.getPrice();
				this.getV1();
				this.getV2();
				this.getV3();
				this.getV4();
				this.getV5();
			},
			/* 奖池块 */
			// 获取日奖池ID, 时间, usdt数量
			async getDayPoolId() {
				let res = await this.$contract.dayPoolId();
				this.dayPoolId = this.$util.hexToDec(res._hex);
				let res2 = await this.$contract.dayPool(this.dayPoolId);
				// 是秒值
				let date = this.$util.hexToDec(res2.date._hex);
				let amount = this.$util.hexToDec(res2.amount._hex);
				this.dayUsdtAmount = this.$util.numberFormat(this.$util.div10e6(amount));
				// 当前时间
				date += 86400;
				let nowTime = Math.floor(Date.now() / 1000);
				// 获取时间后计算出来的倒计时
				let downTime = date - nowTime;
				let test = setInterval(() => {
				  downTime -= 1;
				  this.dayDownTime = this.$util.dayFormat(downTime);
				},1000);
			},
			// 获取周奖池iD
			async getWeekPoolId() {
				let res = await this.$contract.weekPoolId();
				this.weekPoolId = this.$util.hexToDec(res._hex);
				let res2 = await this.$contract.weekPool(this.weekPoolId);
				// 是秒值
				let date = this.$util.hexToDec(res2.date._hex);
				let amount = this.$util.hexToDec(res2.amount._hex);
				this.weekUsdtAmount = this.$util.numberFormat(this.$util.div10e6(amount));
				// 当前时间
				date += 86400 *7
				let nowTime = Math.floor(Date.now() / 1000);
				// 获取时间后计算出来的倒计时
				let downTime = date - nowTime;
				let test = setInterval(() => {
				  downTime -= 1;
				  this.weekDownTime = this.$util.weekFormat(downTime);
				},1000);
			},
			
			/* 输入框块 */
			// 判断用户有没有授权usdt给主和合约
			async isApproveUsdt() {
				let res = await this.$contract.allowanceUsdtMast();
				res = this.$util.div10e6(res);
				if(res > 1) {
					this.isApprove1 = true;
				}else {
					this.isApprove1 = false;
				}
				console.log(res);
			},
			// 判断用户有没有授权eco给主和合约
			async isApproveEco() {
				let res = await this.$contract.allowanceEcoMast();
				res = this.$util.div10e6(res);
				if(res > 1) {
					this.isApprove2 = true;
				}else {
					this.isApprove2 = false;
				}
				console.log(res);
			},
			// 用户授权usdt给主合约
			async approveUsdt() {
				let res = await this.$contract.approveMaster();
				await this.isApproveUsdt();
				console.log(res);
			},
			// 用户授权eco给eco门票合约
			async approveEco() {
				let res = await this.$contract.approveTickToMaster();
				await this.isApproveEco();
				console.log(res);
			},
			// 发送按钮
			async sendOut() {
				let number = this.$util.mul10e6(this.usdtInput);
				let referrer = this.$route.query.referrer;
				console.log('最后传入的值', this.usdtInput, this.$route.query.referrer);
				let res = await this.$contract.join(referrer, number);
				console.log('res:', res);
			},
			
			// 获取当前共振比列
			async getPrice() {
				let res = await this.$contract.price();
				res = this.$util.numberFormat(this.$util.div10e18(this.$util.hexToDec(res._hex)*300));
				this.ecoRatio = res;
			},
			// 输入框内容改变的时候
			inputChange() {
				this.selectColor = '';
			},
			// 选择usdt
			selectU(number) {
				if(number === 0) {
					this.selectColor = '0';
					this.usdtInput = 100;
				}else if(number === 1) {
					this.selectColor = '1';
					this.usdtInput = 300;
				}else if(number === 2) {
					this.selectColor = '2';
					this.usdtInput = 500;
				}else if(number === 3) {
					this.selectColor = '3';
					this.usdtInput = 1000;
				}else if(number === 4) {
					this.selectColor = '4';
					this.usdtInput = 1500;
				}else if(number === 5) {
					this.selectColor = '5';
					this.usdtInput = 2000;
				}else if(number === 6) {
					this.selectColor = '6';
					this.usdtInput = 2500;
				}else if(number === 7) {
					this.selectColor = '7';
					this.usdtInput = 5000;
				}
			},
			
			/* 第一块; v1...v5 */
			async getV1() {
				// 获取钱包默认的地址
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res = await this.$contract.userData(address);
				// 数据
				this.v.nodeLevel = res.nodeLevel;
				let joinTime = this.$util.hexToDec(res.joinTime._hex);
				let downTime = Math.floor((joinTime+86400*100)- Date.now()/1000);
				let test = setInterval(() => {
				  downTime -= 1;
				  this.v.cd100DayString = this.$util.dayTime(downTime);
				},1000);
				this.v.alreadyIncome = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(await this.$contract.takedIncome(address))));
				this.v.dayPoolIncome = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.dayPoolIncome._hex)));
				this.v.weekPoolIncome = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.weekPoolIncome._hex)));
				this.v.dynamicIncome = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.dynamicIncome._hex)));
				this.v.reward = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.reward._hex)));
				let downTime2 = Math.floor(86400 - (Date.now() / 1000 - joinTime) % 86400);
				let test2 = setInterval(() => {
				  downTime2 -= 1;
				  this.v.cdTimeString = this.$util.hoursTime(downTime2);
				},1000);
				this.v.timeReward = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.deposit._hex)*0.005));
				this.v.deposit = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.deposit._hex)));
				let maxIncome = this.$util.hexToDec(res.maxIncome._hex);
				let deposit = this.$util.hexToDec(res.deposit._hex);
				if(maxIncome === 0 || deposit === 0){
					this.v.AcceleratedExit = 0;
				}else {
					this.v.AcceleratedExit = this.$util.numberFormat(maxIncome / deposit);
				}
				this.v.untakeIncome = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.income._hex)));
			},
			// 日奖池收益提现和周奖池收益提现
			async poolWithdraw() {
				let res = await this.$contract.takePoolIncome();
				console.log('v1提现', res)
			},
			// 最大的提现按钮
			async smallWithdraw() {
				let res = await this.$contract.take_master();
				console.log('v12提现', res)
			},
			
			/* 第二块-邀请链接 */
			async getV2() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res = await this.$contract.performances(address);
				this.v2.address2 = address;
				this.v2.address = this.$util.addressCurtail(address);
				// 今日邀请金额
				this.v2.performance = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.performance)));
				// 本轮邀请业绩
				this.v2.wholeLine = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res.wholeLine)));
				let res2 = await this.$contract.userReferrer(address);
				this.v2.referrer = this.$util.addressCurtail(await this.$contract.addressFromHex(res2.referrer));
			},
			// 点击了复制EID邀请链接 - 复制
			async e32() {
				console.log(' 点击了复制EID邀请链接 - 复制');
				let value = this.v2.address2;
				value = window.location.origin + '?referrer=' + value;
				let input = document.createElement("input");
				input.value = value;
				document.body.appendChild(input);
				input.select();
				input.setSelectionRange(0, input.value.length), document.execCommand('Copy');
				document.body.removeChild(input);
			},
			
			/* 第三块-今日冠军推广业绩 */
			async getV3() {
				let res = await this.$contract.dayPoolId();
				let id = this.$util.hexToDec(res._hex);
				// 0是冠军, 1是亚军, 2是季军
				let champion = await this.$contract.dayPoolRank(id, 0);
				// 冠军地址
				champion = await this.$contract.addressFromHex(champion);
				// 亚军地址
				let champion1 = await this.$contract.dayPoolRank(id, 1);
				champion1 = await this.$contract.addressFromHex(champion1);
				// 季军地址
				let champion2 = await this.$contract.dayPoolRank(id, 2);
				champion2 = await this.$contract.addressFromHex(champion2);
				// 今日冠军推荐业绩
				let championPer = await this.$contract.dayPoolValue(id, champion);
				championPer = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(championPer)));
				// 获取今日冠军收益
				let dayTotal = await this.$contract.dayPool(id);
				let res2 = await this.$contract.weekPoolId();
				let id2 = this.$util.hexToDec(res2._hex);
				let weekTotal = await this.$contract.weekPool(id2);
				dayTotal = this.$util.div10e6(this.$util.hexToDec(dayTotal.amount._hex));
				// 上周冠军中奖收益, 用来计算的
				weekTotal = this.$util.div10e6(this.$util.hexToDec(weekTotal.amount._hex));
				// 获取冠军
				let championWeek0 = await this.$contract.weekPoolRank(id2, 0);
				// 周冠军地址
				championWeek0 = await this.$contract.addressFromHex(championWeek0);
				// 获取亚军
				let championWeek1 = await this.$contract.weekPoolRank(id2, 1);
				// 周亚军地址
				championWeek1 = await this.$contract.addressFromHex(championWeek1);
				// 上轮冠军邀请业绩
				let championPer2 = await this.$contract.weekPoolValue(id2, championWeek0);
				championPer2 = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(championPer2)));
				
				let v3 = {
					championPer: championPer,
					championIncome: this.$util.numberFormat(dayTotal * 0.55),
					dayChampion1: this.$util.addressCurtail(champion),
					dayChampion2: this.$util.addressCurtail(champion1),
					dayChampion3: this.$util.addressCurtail(champion2),
					championWeek0Income: this.$util.numberFormat(weekTotal * 0.55),
					championWeek1Income: this.$util.numberFormat(weekTotal * 0.25),
					championWeek0: this.$util.addressCurtail(championWeek0),
					championWeek1: this.$util.addressCurtail(championWeek1),
					championPer2: championPer2
				}
				this.v3 = v3;
			},
			
			/* 第四块-保险池 */
			async getV4() {
				let time = await this.$contract.createdAt();
				time = this.$util.hexToDec(time._hex);
				let downTime = Date.now() / 1000 - time;
				let test = setInterval(() => {
				  downTime -= 1;
				  this.v4 = this.$util.daysFormat(downTime);
				},1000);
				let res2 = await this.$contract.insurance();
				this.pool1 = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res2._hex)));
				let res3 = await this.$contract.sustainable();
				this.pool2 = this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res3._hex)));
			},
			
			/* 第五块-全球节点 */
			async getV5() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res = await this.$contract.wholePerformances(address);
				// 直推业绩
				let wholeLine = res.wholeLine._hex;
				// 合计业绩
				let performance = res.performance._hex;
				console.log('直推业绩', wholeLine, performance)
				
				let res2 = await this.$contract.teamsStats(address);
				
				// 全球节点收益
				let res3 = await this.$contract.nodeIncome(address);
				// 节点焚毁收益
				let res4 = await this.$contract.nodeBurnIncome(address);
				
				let d = {
					performance: this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(wholeLine))),
					// 合计业绩
					performance2: this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(performance))),
					small: this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res2[0]._hex))),
					max: this.$util.numberFormat(this.$util.div10e6(this.$util.hexToDec(res2[1]._hex))),
					// 全球节点收益
					gloablNodeIncome: this.$util.numberFormat(this.$util.div10e6(res3)),
					// 节点焚毁收益
					nodeIcome: this.$util.numberFormat(this.$util.div10e6(res4)),
				};
				this.v5 = d;
				console.log(res2[0]._hex, res2[1]._hex, res3, res4)
			},
			
			// 点击了焚毁按钮
			async nodeBurn() {
				let res = await this.$contract.activateNodeBurn();
				console.log('res', res)
			},
			
			
			
		},
		
	}
</script>

<style>
	/* -----------周日奖池------------ */
	/* 默认样式 */
	.jackpot-layout {
		margin: 20px 0;
	}
	.jackpot-pot {
		width: 239px;
		height: 159px;
		background: #FFFFFF;
		border-radius: 20px;
		border: 1px #F5F5F5 solid;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
	}
	.jackpot-pot > div:nth-child(1) {
		font-weight: bold;
	}
	.jackpot-pot > div:nth-child(3) {
		width: 80%;
		text-align: center;
	}
	
	/* 宽度尺度大于1500时 */
	@media only screen and (min-width: 415px){
		.jackpot-layout {
			display: flex;
			justify-content: center;
		}
		.pot1 {
			margin-right: 30px;
		}
	}
	
	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px){
		.jackpot-layout {
			
		}
		.jackpot-pot {
			width: 100%;
		}
		.pot1 {
			margin-bottom: 20px;
		}
	}
	
	/* ------------输入框和选择U---------- */
	.filling-layout {
		display: flex;
		flex-direction: column;
	}
	.filling-layout > div {
		margin-top: 10px;
	}
	.filling-u {
		display: flex;
		overflow-y: auto;
		margin-bottom: 30px;
	}
	.colorU {
		border: 1px solid #E0E0E0;
		margin-right: 10px;
		padding: 1px 5px;
		color: #808080;
		cursor: pointer;
	}
	.colorU2 {
		border: 1px solid #34CECC;
		margin-right: 10px;
		padding: 1px 5px;
		color: #34CECC;
		cursor: pointer;
	}
	
	.filling-approve2 {
		background: #FFB400;
		color: #FFFFFF;
		text-align: center;
		margin-left: 20px;
		border-radius: 10px;
		line-height: 50px;
		min-width: 100px;
	}
	
	/* 宽度尺度大于1500时 */
	@media only screen and (min-width: 415px){
		.filling-layout {
			width: 75%;
			margin: 0 auto;
			text-align: left;
		}
		/* 输入框 */
		.filling-input-layout {
			display: flex;
		}
		.filling-input:focus{
			outline:none;
		}
		.filling-input {
			padding: 10px 20px;
			font-size: 20px;
			border-radius: 20px 0 0 20px;
			border: 1px solid #F5F5F5;
			height: 30px;
			flex-grow: 6;
		}
		.filling-usdt {
			background: #FFFFFF;
			line-height: 50px;
			text-align: center;
			flex-grow: 2;
		}
		.filling-approve {
			background: #34CECC;
			color: #FFFFFF;
			line-height: 50px;
			text-align: center;
			border-radius: 0 20px 20px 0;
			flex-grow: 2;
		}
	}
	
	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px){
		.filling-layout {
			text-align: left;
		}
		.filling-tips {
			font-size: 15px;
		}
		/* 输入框 */
		.filling-input-layout {
			display: flex;
		}
		.filling-input:focus{
			outline:none;
		}
		.filling-input {
			padding: 8px 15px;
			font-size: 20px;
			border-radius: 20px 0 0 20px;
			border: 1px solid #F5F5F5;
			height: 30px;
			width: 50%;
		}
		.filling-usdt {
			background: #FFFFFF;
			text-align: center;
			flex-grow: 1;
			line-height: 46px;
		}
		.filling-approve {
			background: #34CECC;
			color: #FFFFFF;
			text-align: center;
			border-radius: 0 20px 20px 0;
			flex-grow: 1;
			line-height: 46px;
		}
	}
	
	/* ------------ 余下全部 ------------ */
	.v {
		background: #EDEEF2;
		color: #938AA2;
		padding: 3px 15px;
	}
	.v2 {
		background: #FFF000;
		color: #938AA2;
		padding: 3px 15px;
	}
	
	.v3 > div {
		margin-bottom: 20px;
	}
	
	.v-pot > div:nth-child(3) {
		width: 80%;
		text-align: center;
	}
	
	/* 宽度尺度大于1500时 */
	@media only screen and (min-width: 415px){
		.v4 {
			display: flex;
		}
		.v {
			margin-right: 20px;
		}
		.v2 {
			margin-right: 20px;
		}
		.v-layout {
			display: flex;
			justify-content: space-between;
			margin-bottom: 30px;
		}
		.v-pot {
			background: #FFFFFF;
			border-radius: 20px;
			border: 1px #F5F5F5 solid;
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			align-items: center;
			width: 48%;
			height: 180px;
		}
	}
	
	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px){
		.v {
			margin-bottom: 5px;
		}
		.v2 {
			margin-bottom: 5px;
		}
		.v-layout {
			display: flex;
			flex-direction: column;
			margin-bottom: 30px;
		}
		.v-pot {
			background: #FFFFFF;
			border-radius: 20px;
			border: 1px #F5F5F5 solid;
			display: flex;
			flex-direction: column;
			justify-content: space-around;
			align-items: center;
			height: 160px;
		}
	}
</style>



